.odc-monitoring-overview__metric-accordion {
  padding: 0;
}

.odc-monitoring-overview__alerts-toggle .pf-v5-c-accordion__toggle-text {
  width: 100%;
}

.odc-monitoring-overview__alerts-body .pf-v5-c-accordion__expanded-content-body {
  --pf-v5-c-accordion__expanded-content-body--PaddingTop: var(--pf-v5-global--spacer--sm);
  --pf-v5-c-accordion__expanded-content-body--PaddingRight: 0;
  --pf-v5-c-accordion__expanded-content-body--PaddingBottom: var(--pf-v5-global--spacer--sm);
  --pf-v5-c-accordion__expanded-content-body--PaddingLeft: 0;
}

.odc-monitoring-overview__view-monitoring-dashboards {
  margin: var(--pf-v5-global--spacer--sm) 0;
  text-align: right;
}

.odc-monitoring-overview .pf-v5-c-accordion__expandable-content.pf-m-expanded {
  --pf-v5-c-accordion__expandable-content-body--after--BackgroundColor: transparent;
}

.odc-monitoring-overview .pf-v5-c-accordion__toggle.pf-m-expanded {
  --pf-v5-c-accordion__toggle--after--BackgroundColor: transparent;
}

.odc-monitoring-overview__empty-state-icon {
  color: var(--pf-v5-global--icon--Color--light);
  font-size: var(--pf-v5-global--FontSize--4xl);
}

